import React from 'react';
import { Util } from '../util/util';

enum ProgressColor {
    GOOD = 'green',
    WARN = 'orange',
    BAD = 'red',
}

const calculateProgressColor = (min: number): ProgressColor =>
    min < 15 ? ProgressColor.GOOD : min < 30 ? ProgressColor.WARN : ProgressColor.BAD;

const calculateWidthPercent = (min: number): number => Math.min(100, min / 0.45);
const calculateWidth = (min: number): string => calculateWidthPercent(min) + '%';

